.barra_pesquisa{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

#botao_pesquisar{
    border-radius: 50%;
    color: rgb(235, 231, 240);
    background-color: rgb(79, 33, 77);
    width: 3em;
    height: 3em;
}
#barra_escrita{
    width: 20em;
    height: 3em;
    margin: 5px;
    outline: unset;
    border: 1px solid #a0005c;
    font-size: 1em;
    border-radius: 10px;
}

#barra_escrita:focus{
    background-color:wheat;
}


/*CSS <=480px && > 380px*/
@media(max-width: 480px){
    #botao_pesquisar{
        border-radius: 50%;
        color: rgb(235, 231, 240);
        background-color: rgb(79, 33, 77);
        width: 2em;
        height: 2em;
    }
    #barra_escrita{
        width: 13em;
        height: 2em;
        margin: 5px;
        outline: unset;
        border: 1px solid #a0005c;
        font-size: 1em;
    }
    
}
