.logo_principal{
      background-color: #993399e2;
      display:flex;
      justify-content:space-evenly;
      align-items: center;
      margin-bottom: 10px;
      
  }
  
  .logo_principal img{
    border-radius: 50%;
      padding: 5px;
      width: 200px;
      height: 200px;
      display: flex;
      justify-content: center;
      background-color: rgb(246, 246, 246);
      margin: 5px;
    
  }

  .logo_principal h1{
    background: white;
    border-radius: 20px;
    width:180px;
    padding: 5px;
    padding-left: 20px;
    color: #993399;
  }

  @media(max-width: 750px) and (min-width:480px){
    .logo_principal{
      background-color: #993399;
      display:flex;
      justify-content:space-evenly;
      align-items: center;
      margin-bottom: 10px;
      
  }
  
  .logo_principal img{
    border-radius: 50%;
      padding: 5px;
      width: 100px;
      height: 100px;
      display: flex;
      justify-content: center;
      background-color: rgb(246, 246, 246);
      margin: 5px;
    
  }

  .logo_principal h1{
    background: white;
    border-radius: 20px;
    width:100px;
    padding: 5px;
    font-size: 1.6em;
  }

  }

