.cadastro label{
  display: block;
  justify-content: center;
  margin: 10px 0px 30px 0px;
  color: #fff;
  font-size: 1.2em;
  font-weight: bold;
  
}
.form_control{
  display: block;
  height: 35px;
  width: 100%;
  border-radius: 20px;
  

}
.cadastro{
  padding: 30px;
  width: 400px;
  background-color: #9700fce5;
  border-radius: 20px;
  
  
}
.corpo_cadastro{
  display: flex;
  justify-content: center;
  padding: 50px;
  border-radius: 20px;
}

.cadastro .botao_criar_post{
  width: 100%;
}

@media(max-width: 320px) and (min-width:280px){
  .cadastro label{
    display: block;
    justify-content: center;
    margin: 10px 0px 30px 0px;
    color: #fff;
    font-size: 1em;
    font-weight: bold;
    
  }
  .form_control{
    display: block;
    height: 10px;
    width: 100%;
    border-radius: 20px;
    
  
  }
  .cadastro{
    padding: 20px;
    width: 400px;
    background-color: #9700fce5;
    border-radius: 20px;
    
    
  }
  .corpo_cadastro{
    display: flex;
    justify-content: center;
    padding: 40px;
    border-radius: 20px;
  }

  .inputs_cadastro{
    height: 30px;
    font-size: 0.7em;
  }

  #titulo_cadastro{
    font-size: 1.5em;
  }


  
  
}

@media(max-width: 750px) and (min-width:320px){

  .inputs_cadastro{
    
    font-size: 0.8em;

  }
  #titulo_cadastro{
    font-size: 2em;
  }

}
