.titulo_noticia{
    display: flex;
    justify-content: center;
    padding: 20px;
}

.titulo_noticia h1{
    background-color: rgba(102, 3, 102,0.7);
    border-radius: 10px;
    color: aliceblue;
    padding: 10px;
}

.imagem_noticia{
    display: flex;
    justify-content: center;
}

.imagem_noticia img{
    width: 900px;
    height: 500px;
    padding: 10px;
    margin-bottom: 20px;
}

.texto_noticia{
    display: flex;
    justify-content: center;
    margin-bottom: 100px;
}

.texto_noticia p{
    border: 5px solid rgba(0, 0, 0, 0.5);
    background-color: rgba(102, 3, 102,0.7);
    color: #080808;
    padding: 50px;
    border-radius: 10px;
    font-size: 1.5em;
    color: aliceblue;
}

/*---------COMENTARIOS----------*/

.comentarios{
    border-top: 1px solid #080808 ;
}

.titulo_comentario_noticia{
    display: flex;
    justify-content: center;
}

.identificacao_comentario{
    margin-left: 4px;

}

/*-------CAIXA DE COMENTARIOS--------*/
.caixa_comentario_noticia{
    display: flex;
    justify-content: center;
    margin: 40px 40px 80px 40px;
}

.corpo_caixa_comentario{
    background-image: url("https://img.freepik.com/fotos-gratis/parede-de-concreto-antiga_1249-225.jpg?size=626&ext=jpg&ga=GA1.2.1382747142.1667683050&semt=sph");
    box-shadow: 0 50px 50px -20px rgba(0, 0, 0, 0.85);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 40px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
}
.avatar_comentarios{
    border-radius: 50%;
    margin: 7px;
}

.caixa_comentario_noticia button{
    border-radius: 50%;
    border: 0px;
    width: 50px;
    height: 50px;
    color: rgb(235, 231, 240);
    background-color: rgb(79, 33, 77);

    margin: 8px;
}

/*--------COMENT√ÅRIOS ENVIADOS--------*/
.comentarios_usuarios_noticia{
    display: flex;
    justify-content: center;
}
.comentarios_usuarios_noticia ul{
    border: 5px solid rgba(0, 0, 0, 0.821);
    border-radius: 10px;
    padding: 20px;
    background-image: url("https://img.freepik.com/fotos-gratis/parede-de-concreto-antiga_1249-225.jpg?size=626&ext=jpg&ga=GA1.2.1382747142.1667683050&semt=sph");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.alinhando_comentarios{
    display: flex;
    justify-content: space-between ;
}
.alinhando_comentarios p{
    background-color:  rgba(248, 119, 33, 0.821);
    align-items: center;
    height: 30px;
    padding: 5px;
    border-radius: 10px;
    font-weight: bold;
}

.comentario_enviado{
    background-color:  rgba(102, 3, 102,0.7);
    color: aliceblue;
    padding: 5px;
    border-radius: 10px;
    
}

@media(max-width: 1200px) and (min-width:750px){
    .imagem_noticia img{
        width: 71%;
        height: 50%;
        padding: 10px;
        margin-bottom: 20px;
    }
    
    .texto_noticia p{
        border: 5px solid rgba(0, 0, 0, 0.5);
        background-color: rgba(102, 3, 102,0.7);
        color: #080808;
        padding: 50px;
        border-radius: 10px;
        font-size: 1.3em;
        color: aliceblue;
        width: 70%;
    }
    
    

}


@media(max-width: 750px) and (min-width:480px){
    .imagem_noticia img{
        width: 85%;
        height: 70%;
        padding: 10px;
        margin-bottom: 20px;
    }

    .texto_noticia p{
        border: 5px solid rgba(0, 0, 0, 0.5);
        background-color: rgba(102, 3, 102,0.7);
        color: #080808;
        padding: 30px;
        border-radius: 10px;
        font-size: 1.1em;
        color: aliceblue;
        width: 70%;
    }
    
}

@media(max-width: 551px){
    
    .caixa_comentario_noticia{
        display: flex;
        justify-content: center;
    }

    .corpo_caixa_comentario{
        display: block;
    }

}

@media(max-width: 480px) and (min-width:320px){
    .titulo_noticia h1{
        background-color: rgba(102, 3, 102,0.7);
        border-radius: 10px;
        color: aliceblue;
        padding: 5px;
        font-size: 1.3em;
    }
    .imagem_noticia img{
        width: 95%;
        height: 75%;
        padding: 10px;
        margin-bottom: 20px;
    }

    .texto_noticia p{
        border: 5px solid rgba(0, 0, 0, 0.5);
        background-color: rgba(102, 3, 102,0.7);
        color: #080808;
        padding: 5px;
        border-radius: 10px;
        font-size: 0.95em;
        color: aliceblue;
        width: 70%;
    }
    
}

@media(max-width: 320px) and (min-width:280px){
    .titulo_noticia h1{
        background-color: rgba(102, 3, 102,0.7);
        border-radius: 10px;
        color: aliceblue;
        padding: 5px;
    }
    .imagem_noticia img{
        width: 95%;
        height: 75%;
        padding: 10px;
        margin-bottom: 20px;
    }

    .texto_noticia p{
        border: 5px solid rgba(0, 0, 0, 0.5);
        background-color: rgba(102, 3, 102,0.7);
        color: #080808;
        padding: 0px;
        border-radius: 10px;
        font-size: 0.9em;
        color: aliceblue;
        width: 70%;
    }
    /*-------CAIXA DE COMENTARIOS--------*/
    .caixa_comentario_noticia{
        display: flex;
        justify-content: center;
        margin: 20px 20px 60px 20px;
    }
}
