.nav{
    display: flex;
    justify-content: center;
    height: 30px;
}

nav a{
    margin-right: 10px;
    padding: 5px;
    text-decoration: none;
    color:#000 ;
    font-weight: bold;
}

nav a:hover{
    color: rgb(0, 0, 0);
}

.active{
    background-color: rgba(248, 119, 33, 0.821);
    
    border-radius: 10px;
}


@media(max-width: 1000px) and (min-width:750px){
    nav a{
        margin-right: 10px;
        padding: 10px;
        text-decoration: none;
        color:#000 ;
        font-weight: bold;
        font-size: 1.4em;
    }
}

@media(max-width: 750px) and (min-width:480px){
    nav a{
        margin-right: 0px;
        padding: 5px;
        text-decoration: none;
        color:#000 ;
        font-weight: bold;
        font-size: 1em;
    }

    
}

@media(max-width: 480px) and (min-width:380px){
    nav a{
        margin-right: 0px;
        padding: 5px;
        text-decoration: none;
        color:#000 ;
        font-weight: bold;
        font-size: 0.9em;
    }
}

@media(max-width: 380px) and (min-width:280px){
    nav a{
        margin-right: 0px;
        padding: 5px;
        text-decoration: none;
        color:#000 ;
        font-weight: bold;
        font-size: 0.8em;
    }
}