.carrossel_home{
    padding-left: 200px;
    padding-right: 200px;
    margin-bottom: 100px;
}

.img_carrossel_home{
    height: 600px;
}

#titulo_carrossel_home{
    display: flex;
    justify-content: center;
}

.descricao_img_carrossel{
    color: rgb(255, 255, 255);
    font-size: 1.5em;
    background-color: #993399;
    border-radius: 10px;
    font-weight: bold; 
}

.noticias_home{
    width: 50%;
    margin-left: 5%; 
}

.noticias_individuais{
    display: flex;
    margin-right: 10px;
    
    border-bottom: 1px solid black;
    padding: 20px;
    
}
.noticias_individuais img{
    width: 400px;
    height: 200px;
    margin-right: 30px;
}

.descricao_noticia_home p{
    font-size: 1em;
}

.titulo_principal_noticia{
 
 padding: 10px 0px 10px 45px;
}



/* CSS <=1200px && >=750px */
@media(max-width: 1200px) and (min-width:750px){
    .carrossel_home{
        padding-left: 70px;
        padding-right: 70px;
        margin-bottom: 25px;
    }

    .img_carrossel_home{
    
        height: 400px;
        width: auto;
    }
    
    #titulo_carrossel_home{
        display: flex;
        justify-content: center;
    }

    .carousel-inner{
        padding: 0px;   
    }
    .descricao_img_carrossel{
        color: rgb(255, 255, 255);
        font-size: 1em;
        background-color: #993399;
        border-radius: 10px;
        font-weight: bold; 
    }
}

/* CSS <=750px && >=750px */
@media(max-width: 750px) and (min-width:480px){
    
    .carrossel_home{
        padding-left: 25px;
        padding-right: 25px;
        margin-bottom: 25px;
    }

    .img_carrossel_home{
    
        height: 300px;
        width: auto;
    }
    
    #titulo_carrossel_home{
        display: flex;
        justify-content: center;
    }

    .carousel-inner{
        padding: 0px;   
    }
}
/*CSS NOTICIAS HOME <=641px && >=480px*/
@media(max-width: 641px) and (min-width:480px){
    .noticias_individuais{
        display: block;
        border-bottom: 1px solid black;
        justify-content: center;
        margin-right: 0px;
        
    }
    .noticias_individuais img{
        width: 400px;
        height: 250px;
        border-radius: 10px;
        
        
    }
    
    .descricao_noticia_home p{
        font-size: 1em;
        display: flex;
        justify-content: center;
    }
    .descricao_noticia_home h3{
        display: flex;
        justify-content: center;
    }
    

    .noticias_home{
        width: auto;
        margin: 0px;
    }

    .descricao_noticia_home h3{
        display: flex;
        justify-content: center;
        font-size: 1.3em;
    }
    
    .titulo_principal_noticia{
        display: flex;
        justify-content: center;
        padding: 0px;
    }

    .alinha_img_noticia{
        display: flex;
        justify-content: center;
    }
    }

/*FIM CSS <=750px && >=480 */

