.corpo_carrossel_recomenda{
    padding-right: 22em;
    padding-left: 22em;
    margin-bottom: 150px;
    

}

.img_recomendacoes{
    height: 400px;
    
}
.titulos_recomendacoes{
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
}
.titulos_recomendacoes h1{
    background-color: #9700fce5;
    border-radius: 20px; 
    padding-left: 70px;
    padding-right: 70px;
    color: aliceblue;

}

.botoes_carrossel_recomenda{
    
    background-color: rgba(248, 119, 33, 0.821);
}

.post_aling{
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
}

.corpo_post_recomendacoes{
    display: flex;
    justify-content: center;
    /*background-color:#7241ff;*/
    padding: 50px;
    width: 100%;
    border-top: 5px solid rgb(0, 0, 0);
    border-bottom: 5px solid rgb(0, 0, 0);
    border-radius: 10px;
    color: rgb(0, 0, 0);
    background-image: url("https://img.freepik.com/fotos-gratis/parede-de-concreto-antiga_1249-225.jpg?size=626&ext=jpg&ga=GA1.2.1382747142.1667683050&semt=sph");
    box-shadow: 0 50px 50px -20px rgba(0, 0, 0, 0.85);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    
    
  
}

.corpo_post_recomendacoes img{
    border-radius: 10px;
    
}


.corpo_post_recomendacoes span, h1{
    font-weight: bold;
    color:rgb(0, 0, 0);
    padding: 5px;

}

.corpo_post_recomendacoes p{
    font-size: 1.2em;
    font-weight: bold;
    color: black;
    margin-bottom: 50px;
}

.botoes_post_recomendacoes{
    
    background-color: aliceblue;
    
}

.add_post_recomendacoes{
    display: flex;
    justify-content: center;
}

.testando{
    display: flex;
    justify-content: center;
    padding: 20px;
    border-top: 5px solid rgb(0, 0, 0);
    border-bottom: 5px solid rgb(0, 0, 0);
    border-radius: 10px;
    background-color:#7241ff;
    margin-top: 50px;
    background-image: url("https://img.freepik.com/fotos-gratis/parede-de-concreto-antiga_1249-225.jpg?size=626&ext=jpg&ga=GA1.2.1382747142.1667683050&semt=sph");
    box-shadow: 0 50px 50px -20px rgba(0, 0, 0, 0.85);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.testando div{
    margin: 10px;
    display: flex;
    justify-content: center;
}

.testando h2{
    display:flex;
    justify-content: center;
    font-weight: bold;
    color: black;
}

.testando input , textarea{
    border-radius: 10px;
    border: 0px;
    margin: 5px;
    width: 100%;
    padding: 5px;

    
}


.botao_criar_post{
    color: white;
    background-color: #198754;
}

.corpo_post_recomendacoes h1{
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/*CSS <=1200px && > 750px*/
@media(max-width: 1200px) and (min-width:750px){
    .corpo_carrossel_recomenda{
        padding-right: 8em;
        padding-left: 8em;
        margin-bottom: 150px;
    }

    .titulos_recomendacoes h1{
        background-color: #9700fce5;
        border-radius: 20px; 
        padding-left: 50px;
        padding-right: 50px;
        color: aliceblue;
    
    }

    .testando div{
        margin: 15px;
        display: flex;
        justify-content: center;
    }
    
    .testando h2{
        display:flex;
        justify-content: center;
        font-weight: bold;
        color: black;
    }
    
    .testando input , textarea{
        border-radius: 10px;
        border: 0px;
        margin: 8px;
        width: 100%;
        padding: 10px;
        font-size: 1.5em;
    
        
    }
    
    .testando{
        padding: 30px;
    }
}

/*CSS <=750px && > 480px*/
@media(max-width: 750px) and (min-width:480px){
    .corpo_carrossel_recomenda{
        padding-right: 1em;
        padding-left: 1em;
        margin-bottom: 70px;
    }

    .titulos_recomendacoes h1{
        background-color: #9700fce5;
        border-radius: 20px; 
        padding-left: 50px;
        padding-right: 50px;
        color: aliceblue;
        font-size: 1.2em;
    
    }
    .img_recomendacoes{
        height: 300px;
        
    }
    
    .corpo_post_recomendacoes img{
        width: 350px;
        
    }
}

/*CSS <=480px && > 380px*/
@media(max-width: 480px) and (min-width:380px){
    .corpo_carrossel_recomenda{
        padding-right: 10px;
        padding-left: 10px;
        margin-bottom: 70px;
    }

    .titulos_recomendacoes h1{
        background-color: #9700fce5;
        border-radius: 20px; 
        padding-left: 10px;
        padding-right: 10px;
        color: aliceblue;
        font-size: 1.2em;
    
    }
    .img_recomendacoes{
        height: 250px;
        
    }
    
    .corpo_post_recomendacoes img{
        width: 270px;
        height: 150px;
        border-radius: 10px;
        
    }
    
}

/*CSS <=480px && > 380px*/
@media(max-width: 380px) and (min-width:280px){
    .corpo_carrossel_recomenda{
        padding-right: 10px;
        padding-left: 10px;
        margin-bottom: 70px;
    }

    .titulos_recomendacoes h1{
        background-color: #9700fce5;
        border-radius: 20px; 
        padding-left: 10px;
        padding-right: 10px;
        color: aliceblue;
        font-size: 1em;
    
    }
    .img_recomendacoes{
        height: 200px;
        
    }
    
    .corpo_post_recomendacoes img{
        width: 200px;
        height: 100px;
        border-radius: 10px;
        
    }
    .testando{
        width: 250px;
        padding: 10px;
        
    }
    
    
    
}